// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$white-4:                           rgba($color-default-white, 4%);
$white-10:                          rgba($color-default-white, 10%);
$white-30:                          rgba($color-default-white, 30%);
$white-40:                          rgba($color-default-white, 40%);
$white-56:                          rgba($color-default-white, 56%);
$white-70:                          rgba($color-default-white, 70%);
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-accent-main: #b88f64;
$color-accent-second: #141414;
$color-black: #0d0d0d;
$color-nero: #141414;
$color-nero-2: #171717;
$color-peru: #c68449;
$color-barley-corn: #b88f64;
$color-sunset-orange: #ef4747;
$color-dark-pastel-green: #00b33c;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-cormorant:                      "Cormorant", serif;
$ff-geologica:                      "Geologica", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-900:                         0.9s $tf-default;
$trans-1100:                        1.1s $tf-default;
$trans-600:                         0.6s $tf-default;
$trans-500:                         0.5s $tf-default;
$trans-400:                         0.4s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------


$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;

// Container
// ---------------------------------


$containerSideOffsetDesktop:        20px;
$containerSideOffsetMobile:         10px;
